.main-content {
  min-width: 350px;
  max-width: 800px;
}

.section-text,
section p {
  @extend .fs-5;
  @extend .text-muted;
}

h3 {
  @extend .text-muted;
}

section {
  @extend .pb-3;
  @extend .mb-4;
  @extend .border-bottom;
}

.instagram-embed-container {
  max-width: 100%;
}



/* Override Instagram's default inline styles for responsiveness */
iframe.instagram-media {
    max-width: 100% !important;
    width: 500px !important;
    min-width: auto !important;
}

.bsky-embed-container {
  width: 500px;
  max-width: 100%;
